import type { Buffer, Context, Coord, Cursor, Program } from "glyph-engine";
import { num, sdf, vec2, drawbox } from "glyph-engine";

const {drawBox} = drawbox;

const density = " .:-=+*#%@";
const colors = [
  "var(--rosewater)",
  'var(--flamingo)',
  "var(--pink)",
  'var(--mauve)',
  "var(--red)",
  'var(--maroon)',
  "var(--peach)",
  'var(--yellow)',
  "var(--green)",
  'var(--teal)',
  "var(--sky)",
  'var(--sapphire)',
  "var(--blue)",
  'var(--lavender)',
]; //.map((c) => c + "88");
const backgroundColors = ["var(--crust)"].reverse();
//.map((c) => c + "88");

function main(
  coord: Coord,
  context: Context,
  // eslint-disable-next-line @typescript-eslint/no-unused-vars
  _cursor: Cursor,
  // eslint-disable-next-line @typescript-eslint/no-unused-vars
  _buffer: Buffer
) {
  const t = context.time * 0.0001;
  const m = Math.min(context.cols, context.rows);
  const a = context.metrics.aspect;

  const offset = {
    x: context.cols / 10,
    y: context.rows / 10,
  };

  offset.x += Math.sin(t * 0.5) * context.cols * 0.2;
  offset.y += Math.cos(t * 0.5) * context.rows * 0.2;

  let st = {
    x: ((2.0 * (coord.x - offset.x)) / m) * a,
    y: (2.0 * (coord.y - offset.y)) / m,
  };

  st = vec2.rot(st, 0.6 * Math.sin(0.62 * t) * vec2.length(st) * 2.5);
  st = vec2.rot(st, t * 0.2);

  const s = num.map(Math.sin(t), -1, 1, 0.5, 1.8);
  const pt = {
    x: num.fract(st.x * s) - 0.5,
    y: num.fract(st.y * s) - 0.5,
  };

  const r = 0.5 * Math.sin(0.5 * t + st.x * 0.2) + 0.5;

  const d = sdf.sdCircle(pt, r);

  const width = 0.05 + 0.3 * Math.sin(t);

  const k = num.smoothstep(width, width + 0.2, Math.sin(10 * d + t));
  const c = (1.0 - Math.exp(-3 * Math.abs(d))) * k;

  const index = Math.floor(c * (density.length - 1));

  return {
    char: density[index],
    color: colors[Math.floor(c * colors.length)],
    backgroundColor: backgroundColors[Math.floor(c * backgroundColors.length)],
  };
}

